              <el-form-item label="报名号">
                <span>{{examResultInfo.bmh}}</span>
              </el-form-item>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <div>
              <el-form-item label="专业代码">
                <span>{{examResultInfo.bkzydm}}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div>
              <el-form-item label="专业名称">
                <span>{{examResultInfo.bkzymc}}</span>
