                <span>{{examResultInfo.bkxxfsmc}}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div>
              <el-form-item label="总成绩">
                <span>{{examResultInfo.zf}}</span>
              </el-form-item>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <table width="100%" border="1">
            <tr>
              <td colspan="3">各科目成绩</td>
            </tr>
            <tr>
              <th>科目代码</th>
              <th>科目名称</th>
