              <td>{{examResultInfo.zzll}}</td>
            </tr>
            <tr>
              <td>{{examResultInfo.wgym}}</td>
              <td>{{examResultInfo.wgymc}}</td>
              <td>{{examResultInfo.wgy}}</td>
            </tr>
            <tr>
              <td>{{examResultInfo.ywk1m}}</td>
              <td>{{examResultInfo.ywk1mc}}</td>
              <td>{{examResultInfo.ywk1}}</td>
            </tr>
            <tr>
              <td>{{examResultInfo.ywk2m}}</td>
              <td>{{examResultInfo.ywk2mc}}</td>
              <td>{{examResultInfo.ywk2}}</td>
            </tr>
          </table>
        </el-row>
        <br>
        <br>
        <el-form-item>
          <el-row type="flex" justify="center">
            <a type="primary" style="cursor:pointer" @click="download">下载成绩单</a>
          </el-row>
        </el-form-item>
      </el-form>
    </fieldset>
  </div>
</template>
<style scoped>
td {
  text-align: center;
}
th {
  text-align: center;
}
</style>

<script>
import logOff from "@/components/LogOff";
export default {
  name: "examInfo",
  components: { logOff },
  data() {
    var examInfo = {};
    var str = window.sessionStorage.getItem("studentInfo");
    if (str != null) {
      examInfo = JSON.parse(str);
      console.log("examInfo.data()");
      console.log(examInfo);
    }
    return {
      showFlag: true,
      examResultInfo: examInfo
    };
  },
  methods: {
    onclick: function(event) {
      var url = "/api/exam/print?bmh=" + this.examResultInfo.bmh;
      this.$axios.get(url).then(function(res) {});
      console.log(name);
    },
    download: function() {
      var bmh = this.examResultInfo.bmh;
      var xmlResquest = new XMLHttpRequest();
      xmlResquest.open("GET", "/api/exam/print?bmh=" + bmh, true);
      //xmlResquest.setRequestHeader("Content-type", "application/json");
      xmlResquest.responseType = "blob";
      xmlResquest.onload = function(oEvent) {
        var content = xmlResquest.response;
        var elink = document.createElement("a");
        elink.download = bmh + ".pdf";
        elink.style.display = "none";
        var blob = new Blob([content], {
          type: "application/pdf;charset=UTF-8"
        });
        elink.href = URL.createObjectURL(blob);
        document.body.appendChild(elink);
        elink.click();
        document.body.removeChild(elink);
      };
      xmlResquest.send();
    }
  }
};
</script>


